h1 {
  color: green;
  font-style: italic;
}

.note {
  color: blue;
  padding-top: 1rem;
  font-size: 1rem;
}

.error {
  color: red;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}

.success {
  color: green;
  background: rgb(191, 233, 151);
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}
